.App {
  text-align: center;
}




.App-header {
  background-color: #99ccff;
  min-height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
}

main {
  flex-grow: 1;
  text-align: center;
  padding: 25px;
}

footer {
  padding: 50px;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #EEEEEE;
  font-size: medium;
  text-align: center;
  color: #000000;
}
header {
  padding: 25px;
}

